.anchor {
    display: inline-block;
    vertical-align: middle;
    transform: perspective(1px) translateZ(0);
    box-shadow: 0 0 1px rgba(0, 0, 0, 0);
    position: relative;
    overflow: hidden;
    cursor: pointer;
}
.anchor:before {
    content: "";
    position: absolute;
    z-index: -1;
    left: 0;
    right: 100%;
    bottom: 0;
    background: #B70606;
    height: 2.5px;
    transition-property: right;
    transition-duration: 0.5s;
    transition-timing-function: ease-out;
}
.anchor:hover:before, .anchor:focus:before, .anchor:active:before {
    right:0;
}

